import React, { useState, useEffect } from "react";
import axios from "../utils/axios";
import { SWRConfig } from "swr";
import { seo } from "../helpers/seo";
import { Loading } from "../components";
import * as ROUTES from "../constants/apiRoutes";

const initialState = {
  user: null,
  host: {},
};

export const Context = React.createContext();

const Store = ({ children }) => {
  const [state, setState] = useState(initialState);

  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const local = JSON.parse(localStorage.getItem("nuppin::user"));
    let host = {};
    let userLocal = null;

    console.log("origin", document.origin);
    console.log("URL", document.URL);
    console.log("href", document.location.href);
    console.log("host", document.location.host);
    console.log("pathname", document.location.pathname);

    axios
      .get(`${ROUTES.CHECK_DOMAIN}/${document.location.host}`)
      .then((res) => {
        if (res.status == 204) {
          window.location.replace("https://nuppin.com");
        } else {
          host = res.data;
        }
      })
      .catch((error) => {})
      .finally(() => {
        if (local) {
          const { user } = local;
          axios.defaults.headers.Authorization = `Bearer ${user.refresh_token}`;
          userLocal = user;
        }
        const manifest = {
          short_name: host.company.name,
          name: host.company.name,
          start_url: ".",
          display: "standalone",
          theme_color: "#000000",
          background_color: "#ffffff",
        };
        seo({
          title: host.company.name,
          metaDescription: host.company.description,
          favicon: host.company.photo,
          manifest: manifest,
        });
        setState({
          ...state,
          user: userLocal,
          host: host,
        });
        setLoading(false);
      });
  }, []);

  if (loading) {
    return <Loading />;
  }

  function setLocalStorage(user) {
    localStorage.setItem("nuppin::user", JSON.stringify({ user: user }));
    axios.defaults.headers.Authorization = `Bearer ${user.refresh_token}`;
    setState({ user: user });
  }

  function removeLocalStorage(key) {
    localStorage.removeItem(key);
    axios.defaults.headers.Authorization = null;
    setState({ user: null });
  }
  return (
    <SWRConfig value={{ fetcher: (url) => axios.get(url).then((res) => res.data) }}>
      <Context.Provider value={{ state, setState, setLocalStorage, removeLocalStorage }}>{children}</Context.Provider>
    </SWRConfig>
  );
};

export default Store;
