import React, { useContext } from "react";
import { BrowserRouter as Router, Switch } from "react-router-dom";
import { Browse, SignIn, ProductDetail, ConfirmCode, Register, Cart, Orders, OrderDetail } from "./pages";
import * as ROUTES from "./constants/routes";
import { IsUserRedirect, ProtectedRoute } from "./helpers/routes";
import { Context } from "./store/Store";

export function App() {
  const { state } = useContext(Context);
  const user = state?.user;

  return (
    <Router>
      <Switch>
        <IsUserRedirect user={user} loggedInPath={ROUTES.HOME} path={ROUTES.SIGN_IN}>
          <SignIn />
        </IsUserRedirect>

        <IsUserRedirect user={user} loggedInPath={ROUTES.HOME} path={ROUTES.CONFIRM_CODE}>
          <ConfirmCode />
        </IsUserRedirect>

        <IsUserRedirect user={user} loggedInPath={ROUTES.HOME} path={ROUTES.REGISTER}>
          <Register />
        </IsUserRedirect>

        <ProtectedRoute user={true} path={ROUTES.PRODUCT_DETAIL}>
          <ProductDetail />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.CART}>
          <Cart />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.ORDERS}>
          <Orders />
        </ProtectedRoute>

        <ProtectedRoute exact user={true} path={ROUTES.HOME}>
          <Browse />
        </ProtectedRoute>

        <ProtectedRoute exact user={true} path={ROUTES.ORDER_DETAIL}>
          <OrderDetail />
        </ProtectedRoute>

        <IsUserRedirect user={user} loggedInPath={ROUTES.HOME} path={ROUTES.HOME}>
          <Browse />
        </IsUserRedirect>
      </Switch>
    </Router>
  );
}
